import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Helmet } from 'react-helmet';
import { Upload, FileUp } from 'lucide-react';

const TempMail = () => {
    const [isDragging, setIsDragging] = useState(false);
    const [files, setFiles] = useState([]);
    const [sending, setSending] = useState(false);
    const [receiving, setReceiving] = useState(false);
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');
    const [recipient, setRecipient] = useState('');
    const [subject, setSubject] = useState('');
    const [body, setBody] = useState('');
    const [emails, setEmails] = useState([]);

    // Handle Drag and Drop for Attachments
    const handleDragEnter = (e) => {
        e.preventDefault();
        setIsDragging(true);
    };

    const handleDragLeave = (e) => {
        e.preventDefault();
        setIsDragging(false);
    };

    const handleDragOver = (e) => {
        e.preventDefault();
    };

    const handleDrop = (e) => {
        e.preventDefault();
        setIsDragging(false);
        const droppedFiles = Array.from(e.dataTransfer.files);
        setFiles(prevFiles => [...prevFiles, ...droppedFiles]);
    };

    const handleFileChange = (e) => {
        setError('');
        setSuccess('');
        const selectedFiles = Array.from(e.target.files);
        setFiles(prevFiles => [...prevFiles, ...selectedFiles]);
    };

    // Send Email
    const handleSendEmail = async () => {
        if (!recipient || !subject || !body) {
            setError('All fields must be filled.');
            return;
        }

        setSending(true);
        setError('');
        setSuccess('');

        const formData = new FormData();
        formData.append('recipient', recipient);
        formData.append('subject', subject);
        formData.append('body', body);
        files.forEach(file => formData.append('attachments', file));

        try {
            await axios.post('/api/send-email', formData);
            setFiles([]);
            setRecipient('');
            setSubject('');
            setBody('');
            setSuccess('Email sent successfully!');
        } catch (err) {
            console.error('Error sending email:', err);
            setError('Failed to send email. Please try again.');
        } finally {
            setSending(false);
        }
    };

    const [emailNumber, setEmailNumber] = useState(0);
    // Fetch Received Emails
    const fetchEmails = async () => {
        setReceiving(true);
        setError('');
        try {
            // Send GET request to the backend to retrieve emails
            const response = await axios.get('/api/receive-emails');
            setEmails(response.data.emails);
            setEmailNumber(response.data.emailNumber)

        } catch (err) {
            console.error('Error fetching emails:', err);
            setError('Failed to fetch emails.');
        } finally {
            setReceiving(false);
        }
    };



    // Fetch emails when component loads
    useEffect(() => {
        // fetchEmailsNumber();
        fetchEmails();
    }, []);




    return (
        <>
            <Helmet>
                <title>Temporary Email Service</title>
                <meta name="description" content="Send and receive temporary emails with attachments." />
            </Helmet>

            <main className="max-w-3/4 h-full flex flex-col items-center min-h-screen">
                <div className="container bg-white  md:p-10 rounded-lg mx-auto grid grid-cols-1 md:grid-cols-12 gap-1">
                    <div className="max-w-2xl mx-auto mt-10 p-8 bg-gradient-to-br from-white-900 to-white-800 rounded-xl shadow-2xl md:col-span-3">
                        <h1 className="text-4xl font-bold text-center mb-4 text-gray-800">Send Temporary Email</h1>

                        {/* Email Fields */}
                        <input
                            type="email"
                            placeholder="Recipient Email"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={recipient}
                            onChange={(e) => setRecipient(e.target.value)}
                        />
                        <input
                            type="text"
                            placeholder="Subject"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={subject}
                            onChange={(e) => setSubject(e.target.value)}
                        />
                        <textarea
                            placeholder="Email Body"
                            className="w-full mb-4 p-2 border border-gray-300 rounded-lg"
                            value={body}
                            onChange={(e) => setBody(e.target.value)}
                        />

                        {/* Attachments Drag and Drop */}
                        <div
                            className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${isDragging ? 'border-blue-500 bg-blue-50' : 'border-gray-300'}`}
                            onDragEnter={handleDragEnter}
                            onDragLeave={handleDragLeave}
                            onDragOver={handleDragOver}
                            onDrop={handleDrop}
                        >
                            <input
                                type="file"
                                multiple
                                className="hidden"
                                id="file-input"
                                onChange={handleFileChange}
                            />
                            <label
                                htmlFor="file-input"
                                className="bg-gray-600 hover:bg-gray-300 text-white font-bold py-3 px-6 rounded-lg cursor-pointer w-auto inline-flex items-center"
                            >
                                <FileUp className="mr-2" size={20} />
                                Attach Files
                            </label>
                            <p className="mt-2 text-gray-500">or drag and drop files here</p>
                        </div>

                        {/* Error and Success Messages */}
                        {error && <p className="text-red-500 mt-4">{error}</p>}
                        {success && <p className="text-green-500 mt-4">{success}</p>}

                        {/* Send Button */}
                        <button
                            onClick={handleSendEmail}
                            disabled={sending}
                            className="bg-main-color hover:bg-blue-300 text-white font-bold py-3 px-6 rounded-lg cursor-pointer mt-4 inline-flex items-center"
                        >
                            {sending ? 'Sending...' : 'Send Email'}
                        </button>
                    </div>

                    {/* Display Received Emails */}
                    <div className="mt-10 p-8 bg-gradient-to-br from-white-900 to-white-800 rounded-xl shadow-2xl md:col-span-9">
                        <h1 className="text-4xl font-bold text-center mb-4 text-gray-800">Received Emails (hashtagme.972@gmail.com) <span className='text-yellow-300 bg-red-600 px-4 rounded-full'> {emailNumber}</span>

                            <span onClick={() => fetchEmails()} className='cursor-pointer float-right'>
                                <svg fill="#000000" width="40px" height="40px" viewBox="0 0 32 32" version="1.1" xmlns="http://www.w3.org/2000/svg"><g id="SVGRepo_bgCarrier" stroke-width="0"></g><g id="SVGRepo_tracerCarrier" stroke-linecap="round" stroke-linejoin="round"></g><g id="SVGRepo_iconCarrier"> <path d="M15.977 0c-7.994 0-14.498 6.504-14.498 14.498 0 7.514 5.79 13.798 13.236 14.44l-2.87 1.455c-0.354 0.195-0.566 0.632-0.355 0.977l0.101 0.262c0.211 0.346 0.668 0.468 1.021 0.274l4.791-2.453c0.006-0.004 0.012-0.003 0.019-0.007l0.322-0.176c0.177-0.098 0.295-0.257 0.342-0.434 0.049-0.177 0.027-0.375-0.079-0.547l-0.191-0.313c-0.003-0.006-0.009-0.010-0.012-0.015l-2.959-4.624c-0.21-0.346-0.666-0.468-1.021-0.274l-0.232 0.162c-0.354 0.194-0.378 0.694-0.168 1.038l1.746 2.709c-0.009-0-0.018-0.004-0.027-0.005-6.54-0.429-11.662-5.907-11.662-12.47 0-6.891 5.607-12.498 12.498-12.498 6.892 0 12.53 5.606 12.53 12.498 0 3.968-1.823 7.613-5 9.999-0.442 0.332-0.53 0.959-0.199 1.401 0.332 0.442 0.959 0.531 1.401 0.199 3.686-2.768 5.799-6.996 5.799-11.598-0-7.994-6.536-14.498-14.53-14.498z"></path> </g></svg>
                            </span>
                        </h1>



                        {receiving ? (

                            <p className='text-center'>
                                <span className='loader'></span> <br />
                                Loading emails...</p>

                        ) : (
                            <ul className="text-gray-700">
                                {emails.length > 0 ? (
                                    emails.map((email, index) => (
                                        <li key={index} className="mb-6 p-4 border rounded-lg shadow-md bg-white">
                                            <h2 className="text-lg font-semibold text-gray-800">
                                                <strong>From:</strong> {email.from}, Date: {email.date}
                                            </h2>
                                            <h3 className="text-md font-medium text-gray-600">
                                                <strong>Subject:</strong> {email.subject}
                                            </h3>
                                            <p className="mt-2 text-gray-500">
                                                <strong>Body:</strong> {email.body}
                                            </p>
                                        </li>
                                    ))
                                ) : (
                                    <p className="text-gray-500 text-center">No emails received yet.</p>
                                )}
                            </ul>
                        )}
                    </div>


                </div>
            </main>
        </>
    );
};

export default TempMail;
